@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;600&display=swap);
.header {
  background: #e6e6e6d3;
  width: 100%;
  height: 100px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.header-inner {
  width: clamp(400px, 50vw, 800px);
  height: 100%;
  padding: 10px;
  align-items: center;
}

.header-inner > a {
  display: block;
  padding: 20px clamp(3px, 1vw, 10px);
  margin-left: clamp(2px, 0.5vw, 20px);
  font-size: clamp(15px, 3vw, 20px);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: 0.4s ease-out;
}

.header-inner > a:hover {
  color: var(--secondary-color);
}

.social-links {
  margin-top: 50px;
  width: clamp(370px, 50vw, 500px);
  height: 100px;
  justify-content: space-around;
}

.social-icon {
  color: var(--text-color);
  font-size: clamp(40px, 5vw, 50px);
  cursor: pointer;
  transition: 0.3s ease-in;
}

.social-icon:hover {
  color: var(--secondary-color);
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}
#releases {
  height: auto;
}

.releases-container {
  align-content: flex-start;
  width: clamp(400px, 50vw, 800px);
  min-height: 1000px;
  flex-wrap: wrap;
}

.release {
  background-color: var(--highlight-color);
  display: grid;
  place-items: center;
  place-content: center;
  /* background-color: var(--highlight-color); */
  width: clamp(240px, 50vw, 300px);
  height: clamp(320px, 50vw, 380px);
  margin: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.4s ease;
}

.release:hover {
  -webkit-filter: brightness(0.95);
          filter: brightness(0.95);
}

.release-text {
  margin: 1px;
  padding: 0 20px;
  color: var(--text-color);
  line-height: 20px;
  text-align: center;
  width: 240px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.release-info {
  margin: 15px 0;
}

.cover {
  width: clamp(200px, 30vw, 260px);
  height: clamp(200px, 30vw, 260px);
  /* box-shadow: 5px 7px 20px rgba(30,30,30,0.15); */
  border-radius: 3px;
}

#artist {
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 20px;
}

#title {
  font-weight: 200;
  font-size: 15px;
}

.about-container {
  width: clamp(400px, 50vw, 800px);
  padding: 15px;
  color: var(--text-color);
}

.about-container > h1 {
  font-size: clamp(25px, 2vw, 30px);
  font-weight: 600;
}

.about-container > p {
  font-size: clamp(20px, 2vw, 25px);
  font-weight: 400;
  line-height: 35px;
  letter-spacing: 0.8px;
  text-align: justify;
}
:root {
  --primary-color: #e6e6e6;
  --secondary-color: #2e2e2e;
  --highlight-color: #ececec;
  --text-color: #4a4a4a;
  --text-color-light: rgb(83, 83, 83);
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: 'Rubik', sans-serif;
}

h1, h2, h3 {
  letter-spacing: 2px;
}

a {
  color: #4a4a4a;
  color: var(--text-color);
  text-decoration: none;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.centered {
  justify-content: center;
  align-items: center;
}

.main {
  width: 100%;
  min-height: 100vh;
  background-color: #e6e6e6;
  background-color: var(--primary-color);
  padding: auto 0;
}

.container {
  width: 100%;
  height: 100vh;
  padding: 20px;
}

.logo {
  background-image: url(/static/media/cooroot-logo.76bb7f29.png);
  background-size: clamp(400px, 50vw, 600px);
  background-position: center;
  background-repeat: no-repeat;
  width: clamp(400px, 50vw, 600px);
  height: clamp(400px, 50vw, 600px);
}
